import React, {useState} from "react"

import Styles from "./ReviewBlock.module.scss"

import Slider from "../Slider/Slider"
import Modal from "../Modal/Modal"
import Form from "../Form/Form"
import Button from "../Atoms/Button/Button"
import Text from "../Atoms/Text"

const arr = [
  {
    id: 1,
    photo: "/assets/images/3.svg",
    name: " ",
    date: "08.01.2022",
    comment:
      " ,            .  KPI        -      ,    .  -   ,     ,   .    .",
  },
  {
    id: 2,
    photo: "/assets/icons/avatar.svg",
    name: " ",
    date: "08.01.2022",
    comment:
      "   ,   ,      .      ,      ,    .      .         .",
  },
  {
    id: 3,
    photo: "/assets/images/2.svg",
    name: " ",
    date: "26.12.2021",
    comment:
      "    ilink  .      :    ,   ,   .        ,    ,  ,   .     .  ,      ilink.",
  },
  {
    id: 4,
    photo: "/assets/images/4.svg",
    name: " ",
    date: "16.12.2021",
    comment:
      "       ,      HR-    .       HR-.   .  ,   .    .   ,   ,    .     .  , .",
  },
]

function ReviewBlock() {
  const [modalActive, setModalActive] = useState(false)
  return (
    <div className={Styles.review}>
      <div className={Styles.comments}>
        <div className={Styles.commentsHat}>
          <Text type="h2"></Text>
          <Button add onClick={() => setModalActive(true)}>
             
          </Button>
        </div>

        <Slider slides={arr} />
      </div>

      <div className={Styles.arrows}>
        <button className={Styles.left + " swiper-button-prev"}>
          <img src="/assets/icons/arrow.svg" alt="LeftArrow" />
        </button>
        <button className={Styles.right + " swiper-button-next"}>
          <img src="/assets/icons/arrow.svg" alt="RightArrow" />
        </button>
      </div>

      <Modal active={modalActive} setActive={setModalActive}>
        <Form />
      </Modal>
    </div>
  )
}

export default ReviewBlock
